@import url('normalize.css');
@import url('./fonts.css');
/*@import url('./interface.css');*/

html,
body {
  width: 100%;
  height: 100%;
  overflow: hidden;
  
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  user-select: none;
  cursor: default;
}

.title {
  margin: 0;
  position: absolute;
  left: 10px;
  top: 10px;
  font-size: 80px;
  line-height: 80px;
  font-family: 'NimbusSanL';
  text-transform: uppercase;
  font-weight: normal;
}

.title_absolute {
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  margin: -52.5px auto 0;
  text-align: center;
}

@media screen and (min-width: 768px) {
  .title {
    font-size: 105px;
    line-height: 105px;
  }
}

canvas.is-blurry {
  filter: blur(40px)
}

/** F O O T E R */
footer {
  position: fixed;
  bottom: 15px;
  left: 15px;
  right: 15px;
  color: #000;
  font-family: 'NimbusSanL';
  font-size: 15px;
  line-height: 20px;
  letter-spacing: 0.05em;
  z-index: 2;
}

.wrs {
  height: 48px;
  width: 48px;
  display: inline-block;
  vertical-align: middle;
}

.nose,
.eye {
  animation: eyes-change 2.5s infinite linear;
  transform: translateY(0)
}

.smile {
  fill: none;
  stroke: currentColor;
  stroke-width: 1;
  stroke-dashoffset: -9;
  stroke-dasharray: 13 27;
  animation: smile-change 2.5s infinite linear
}


@keyframes smile-change {
  20% {
      stroke-dashoffset: -9;
      stroke-dasharray: 13 27;
  }
  35% {
      stroke-dashoffset: -9;
      stroke-dasharray: 13 27;
  }
  40% {
      stroke-dashoffset: -1;
      stroke-dasharray: 18 27;
  }
  50.714% {
      transform: translateY(-1px);
      stroke-dashoffset: -1;
      stroke-dasharray: 18 27;
  }
  65% {
      transform: translateY(0)
  }
  70% {
      transform: translateY(0);
      stroke-dashoffset: -7;
      stroke-dasharray: 4 20;
  }
  80% {
      transform: translateY(0);
      stroke-dashoffset: -7;
      stroke-dasharray: 4 20;
  }
  90% {
      transform: translateY(0);
      stroke-dashoffset: -7;
      stroke-dasharray: 4 20;
  }
}


@keyframes eyes-change {
  20% {
    transform: translateY(-1px)
  }
  35% {
    transform: translateY(-1px)
  }
  45.714% {
    transform: translateY(-2px)
  }
  60% {
    transform: translateY(-2px)
  }
  80% {
    transform: translateY(1px)
  }
  90% {
    transform: translateY(0)
  }
}

.skip-button {
  display: block;
  padding: 0;
  margin: 0;
  width: 80px;
  height: 80px;
  line-height: 80px;
  position: absolute;
  bottom: -1px;
  right: 0;
  background-color: #000;
  border: none;
  color: #fff;
  cursor: pointer;
  will-change: color;
  font-size: 17px;
  outline: none;
  border-radius: 40px;
  will-change: transform;
  transform: translate3d(0, 0, 0);
  transition: transform 300ms;
}

.skip-button.is-focus {
  transition: none;
}

.skip-button-inner {
  display: inline-block;
  will-change: transform;
  transform: translate3d(0, 0, 0);
  transition: transform 300ms;
}
.skip-button.is-focus .skip-button-inner {
  transition: none;
}

@keyframes Wow {
  from {
    transform: scale(1.2);
  }
  15% {
    transform: scale(0.9);
  }
  35% {
    transform: scale(1.05); 
  }
  to {
    transform: scale(1.0);
  }
}

.art-item {
  position: absolute;
  animation: Wow 400ms forwards;
}

.ux-rectangle,
.ux-traingle,
.ux-oval {
  width: 200px;
  height: 200px;
  position: absolute;
}

.ux-fill.is-drag .ux-inner {
  transform: rotate(0deg);
}

.ux-inner {
  width: 100%;
  height: 100%;
  background-color: #adadad;
  transform: rotate(10deg);
  transition: transform 300ms cubic-bezier(1.0,1.0,.45,1.5);
}

.ux-stroke {
  left: 50%;
  top: 50%;
  margin-top: -100px;
  margin-left: -100px;
  border: 3px solid #adadad;
}

.section.is-hidden {
  display: none;
}